var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticStyle:{"text-align":"justify"}},[_c('p',[_vm._v(" BAföG ist eine staat­liche finan­zielle Unter­stützung für jun­ge Men­schen in Deutsch­land. Voll aus­ge­schrie­ben steht BAföG für „Bundes­aus­bil­dungs­för­derungs­gesetz“. Mit dieser Unter­stützung er­möglicht der Staat jun­gen Er­wachsen­en eine Aus­bil­dung nach ih­ren Inter­essen und Fähig­keiten, un­ab­hän­gig von ihrer finan­ziellen oder so­ziale Situ­ation. ")]),_c('p',[_vm._v(" Ge­fördert wird die erste Aus­bil­dung an berufs­bilden­den Schu­len, Kollegs, Aka­demien und Hoch­schulen. Deu­tsche Staats­ange­hörige kön­nen grund­sätzlich BAföG em­pfan­gen. Bürger*innen der Euro­pä­ischen Union, Migrant*innen und Ge­flüch­tete kön­nen nur dann BAföG er­hal­ten, wenn sie eine Bleibe­perspek­tive in Deutsch­land ha­ben und als ge­sell­schaftlich inte­griert gel­ten. Außer­dem soll­te die schu­lische Aus­bildung oder das Stu­dium vor dem 30. Lebens­jahr be­gon­nen wer­den. Bei einem Master­studium soll­te der Beginn vor dem 35. Lebens­jahr lie­gen. ")]),_c('p',[_vm._v(" Ob eine Per­son förder­fähig ist, über­prüft das BAföG-Amt. Ein zen­trales Kri­te­rium ist da­bei, ob die Aus­bil­dung mit eige­nen Mit­teln oder durch die El­tern, den Ehe­gatten oder ein­ge­tra­genen Lebens­partner selbstän­dig fi­nan­ziert wer­den kann. Eine Teil­förde­rung er­hal­ten Studie­rende, wenn die El­tern oder Ehe­gat­te bzw. Lebens­part­ner Tei­le der Aus­bil­dung selbstän­dig über­neh­men kön­nen. Rei­chen die finan­ziellen Mit­tel der Fami­lie nicht aus, wird eine Voll­för­de­rung ge­währt. Um BAföG zu be­zie­hen, muss ein Nach­weis über eine Auf­nahme an einer Schu­le oder einer Hoch­schule er­bracht wer­den. Ab dem 5. Se­mester müs­sen zu­sätzlich Leis­tungs­nach­weise an das BAföG-Amt ge­schickt wer­den. Fünf Jah­re nach dem En­de der För­derungs­höchst­dauer muss mit der Rück­zah­lung der im Stu­diun er­hal­tenen BAföG-För­derung be­gonnen wer­den. Dabei wer­den durch den Staat kei­ne Zin­sen be­rechnet. ")])])}]

