. Der freie Zusam­men­schluss von Student*innen­schaf­ten und das Deutsche Stu­den­ten­werk for­dern eine Er­höhung des BAföG-Satzes. Zum Sommer­se­mester schafft Ba­den-Württem­berg (Grüne/SPD) all­ge­meine Stu­dien­ge­büh­ren ab, im da­rauf­fol­genden Win­ter­se­mester zie­hen Ham­burg (SPD) und Bayern (CDU/FDP) nach. In Bayern wird das Ge­setz be­schlossen, nach­dem ein durch die Freien Wäh­ler ini­tiiertes Volks­be­geh­ren zur Ab­schaffung Er­folg hatte. ")])]):_vm._e(),(_vm.slider.value == 75)?_c('div',{staticClass:"pa-5"},[_c('h2',[_vm._v("2013")]),_c('p',{staticClass:"year-texts"},[_vm._v(" Ob­wohl die För­der­si­tu­ation sich zum Vor­jahr nicht stark ver­ändert hat, konn­ten sich Bund und Län­der nicht auf eine Ver­ände­rung des BAföGs ei­nigen. ")])]):_vm._e(),(_vm.slider.value == 80)?_c('div',{staticClass:"pa-5"},[_c('h2',[_vm._v("2014")]),_c('p',{staticClass:"year-texts"},[_vm._v(" Zum Win­ter­se­mes­ter 2014/2015 schafft Nie­der­sach­sen (SPD/Grüne) all­ge­meine Stu­dien­ge­büh­ren als letz­tes Bun­des­land ab. ")])]):_vm._e(),(_vm.slider.value == 85)?_c('div',{staticClass:"pa-5"},[_c('h2',[_vm._v("2015")]),_c('p',{staticClass:"year-texts"},[_vm._v(" Im Rah­men des 25. BAföG-Än­derungs­ge­setz wird die BAföG-Fi­nan­zierung voll­stän­dig durch den Bund über­nom­men. ")])]):_vm._e(),(_vm.slider.value == 90)?_c('div',{staticClass:"pa-5"},[_c('h2',[_vm._v("2016")]),_c('p',{staticClass:"year-texts"},[_vm._v(" Die Ver­än­derun­gen des 25. BAföG-Än­de­rungs­gesetz grei­fen. Die Ein­kommens­frei­be­trä­ge der El­tern wer­den er­höht und ei­gen­es Ver­mö­gen bis 7.500 Euro wird nicht mehr be­rück­sich­tigt. Das von der Bun­des­re­gie­rung de­fi­nier­te Ziel, 110.000 Ge­för­derte zu­sätzlich zu ge­ne­rie­ren, wird je­doch ver­fehlt. ")])]):_vm._e(),(_vm.slider.value == 95)?_c('div',{staticClass:"pa-5"},[_c('h2',[_vm._v("2017")]),_c('p',{staticClass:"year-texts"},[_vm._v(" Der An­teil der Stu­dieren­den, die durch BAföG ge­för­dert wer­den, sinkt kon­tinu­ier­lich.