2018 von 1,6 Millionen deutscher Studierende auf 2,5 Millionen an­ge­stie­gen (eigene Be­rech­nung: Stu­dierenden­daten­satz). Durch Prozent­an­gaben kann somit gegen­über abso­luten Zah­len eine Vergleichs­grund­lage ge­schaf­fen wer­den. ")]),_c('h4',[_vm._v("Wieso wird nur die Anzahl der deutschen Studierenden betrachtet?")]),_c('p',[_vm._v(" In den ver­gan­ge­nen Jah­ren ist auch die Zahl der aus­län­dischen Stu­dieren­den stark an­ge­stie­gen. Mit­glieder dieses Personen­kreises kön­nen je­doch nur in Aus­nahme­fällen BAföG be­an­tra­gen. Um die Zahl der Stu­die­ren­den, die theo­re­tisch BAföG be­an­tra­gen kön­nen, möglichst gut ab­zuschätzen, wur­den daher nur Stu­die­rende mit deutscher Staats­bürger­schaft in die Be­rech­nung ein­be­zogen. Durch diese Maß­nah­me wird der An­teil der BAföG-Empfänger*innen ten­denziell über­schätzt. ")]),_c('h4',[_vm._v("Welche alternativen Berechnungsmethoden gibt es?")]),_c('div',{staticStyle:{"text-align":"center","margin-top":"6px"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("../assets/berechnung.jpg")}}),_c('img',{staticStyle:{"width":"50%"},attrs:{"src":require("../assets/berechnung_leg.jpg")}})]),_c('p',[_vm._v(" Neben der von uns verwendeten Berechnung (obere Linie, hellblau), gibt es weitere alternative Methoden zur Bestimmung der Gefördertenquoten (Daten aus Iost, 2019). Beispielsweise lässt sich die Gefördertenquote unter allen Studierenden betrachten (untere Linie, dunkelblau). Aufgrund der hier enthaltenen ausländischen Studierenden ist die errechnete Gefördertenquote unter allen Studierenden wesentlich niedriger als in unseren Berechnungen. Im Rahmen der Berichte der Bundesregierung nach §35 BAföG wird weiterhin eine Gefördertenquote unter allen Anspruchsberechtigten bestimmt (mittlere Linie, mittelblau). Diese soll theoretisch den Anteil der geförderten Studierenden möglichst gut abbilden. Allerdings schwanken die Definitionskriterien für Anspruchsberechtigte zwischen den Berichten. Insgesamt wird jedoch ersichtlich, dass wir duch unsere Berechnungsmethodik die BAföG-Quote systematisch überschätzen. Qualitativ zeigt sich für alle drei Berechnungsmethoden eine ähnliche Verlaufsform für die Entwicklung des Anteils der BAföG-Empfänger*innen. ")])])]),_c('v-flex',{staticClass:"pa-3",attrs:{"xs12":"","md4":""}},[_c('div',[_c('div',[_c('i',{staticClass:"fas fa-file-alt icon"}),_vm._v(" Textquellen ")]),_c('br'),_c('p',{staticStyle:{"font-size":"smaller"}},[_c('ul',[_c('li',[_vm._v(" Der Tagesspiegel (2001). "),_c('span',{staticStyle:{"font-style":"italic"}},[_vm._v(" Das Jahr 2001: Gleiches Bafög für Ost und West. ")]),_vm._v(" Abgerufen unter: "),_c('a',{attrs:{"href":"https://www.tagesspiegel.de/kultur/das-jahr-2001-gleiches-bafoeg-fuer-ost-und-west/190378.html"}},[_vm._v(" https://www.tagesspiegel.de/​kultur/​das-jahr-2001-gleiches-bafoeg-fuer-ost-und-west/190378.html ")]),_vm._v(". ")]),_c('li',[_vm._v(" Deutsches Studentenwerk (2012). "),_c('span',{staticStyle:{"font-style":"italic"}},[_vm._v(" Das BAföG 2012 erhöhen! ")]),_vm._v(" Abgerufen unter: "),_c('a',{attrs:{"href":"https://www.studentenwerke.de/de/content/das-baf%C3%B6g-2012-erh%C3%B6hen"}},[_vm._v(" https://www.studentenwerke.de/​de/​content/​das-baf%C3%B6g-2012-erh%C3%B6hen ")]),_vm._v(". ")]),_c('li',[_vm._v(" Deutsches Studentenwerk (2019). "),_c('span',{staticStyle:{"font-style":"italic"}},[_vm._v(" Geschichte und Statistik zum BAföG. ")]),_vm._v(" Abgerufen unter: "),_c('a',{attrs:{"href":"https://www.studentenwerke.de/de/content/geschichte-und-statistik-zum-baf%C3%B6g"}},[_vm._v(" https://www.studentenwerke.de/​de/​content/​geschichte-und-statistik-zum-baf%C3%B6g ")]),_vm._v(". ")]),_c('li',[_vm._v(" Iost, Oliver (2013). "),_c('span',{staticStyle:{"font-style":"italic"}},[_vm._v(" Jahresausblick 2013. Was sich für Studierende in Gelddingen ändert. ")]),_vm._v(" Abgerufen unter: "),_c('a',{attrs:{"href":"https://www.bafoeg-rechner.de/Hintergrund/art-1472-jahresausblick-2013.php"}},[_vm._v